.Main {
    background-image: url('../Assets/wave-haikei (3).png') , url('../Assets/wave-haikei (4).png');

    position: relative;
    background-size: 30% 200%, 30% 200%;
    background-repeat: no-repeat,no-repeat;
    background-position: left, right;
    z-index: 7;

/*  animation: name|animation-duration|animation-timing-functiony|animation-delay |animation-iteartion-count|animation-direction; */
    animation: wave 10s ease-in-out 5s 1 alternate both ;
    image-rendering:auto; 


}

@keyframes LinearGradient {

    100% {
        background-position: 100% 50%;
        background-color: #1ab982;
    }

    50% {
       
         background-color:   #107e57;
    }

    0% {
        background-position: 0% 50%;
         background-color:  #014760;
    }


}


@keyframes wave {

    100% {
        
        background-position-y: 100%, 100%;
    }

    0% {
        background-position-y: 0%, 0%;
    }



}