.projectSection {

    height: 200vh;
    width: 100%;
    overflow: visible;
    color: white;

    margin: 5% 0;
}

.projectSection h1 {
    margin: 20px 0;
}

.prjContainer{


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}


.container1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;


    height: 50vh;
    width: 100%;
}

.container2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;


    height: 100vh;
    width: 100%;
}


.prjs {
    align-self: center;
    justify-self: center;

    background-color: rgb(19, 18, 18);
    border-radius: 15px;


    /*offset-x | offset-y | blur radius | spread-radius | color  */
    box-shadow: -10px 0px 20px 3px rgb(0, 0, 0);

    padding: 30px;
    width: 60%;
    height: 30vh;
    text-decoration: none;

    animation: infinite alternate;
    transition: 0.7s ease-in-out;

}

.prjs h1 {

    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    user-select: none;
    /* To disable text selection */
    margin: 0;
}


#work1:hover {

    color: transparent;
    background-color: transparent;
    border: none;
    background-image: url("../Assets/Artist-Portfolio.png");
    background-size: 70% 60%;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;

    transform: rotate(-4deg) translateY(-25px);
    transition: 0.7s ease-in;
}

