@media all and (max-width:721px) {

    .projectSection {

        height: 200vh;
        margin: 20% 0;
    }

    .container1 {
        display: grid;
        grid-template-columns: auto;
        height: 160vh;

    }

    .container2 {
        display: grid;
        grid-template-columns: auto;
        height: 160vh;

    }


    .prjs {
        width: 50%;
        height: 10vh;

    }

    #prj1,
    #prj5,
    #prj7 {
        transition: 0.7s ease-out;
        transform: rotate(-4deg) translateX(-25px);
    }

    #prj2,
    #prj4,
    #prj6 {
        transition: 0.7s ease-out;
        transform: rotate(4deg) translateX(25px);

    }

    #prj1:hover,
    #prj5:hover,
    #prj7:hover {
        transition: 0.7s ease-out;
        transform: translateY(-10px);
    }

    #prj2:hover,
    #prj4:hover,
    #prj6:hover {
        transition: 0.7s ease-out;
        transform: translateY(-10px);

    }




}