.nav {
    background:  rgba(0, 0, 0, 0.15);
    backdrop-filter: blur(3px);
    
    color: white;
    overflow: hidden;
    position:sticky;

    top: 0;
    height: 10vh;
    width: 100%;
    z-index: 9;/* specifies the stack order of an element,
    element with greater stack order is always in front of an element with a lower stack order. */
   
}


.navList ul {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

    margin: 0;
    padding: 0;
}

.navLinks {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    
   
    height: 10vh;
}

.navLogo {
    cursor: pointer;

    font-size: 25px;
    letter-spacing: 3px;
    margin: 10px;
    padding: 10px;
}


.navList li{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    cursor: pointer;

    font-size: 20px;
    letter-spacing: 5px;
    margin: 10px;
    padding: 10px;
}

.navList li:hover{

    color: #fff;
    text-shadow: 
    0px 0px 20px #fff,
    0px 0px 20px rgb(185, 185, 1)
}

.a{
    justify-content: center;
    align-items: center;

    margin: 0;
    padding: 0;
}

.Menu {/*This won't display the menu bar in a screen width greater than 494px*/
    display: none;
}

.menuLinks{
    display: none;
   
}




@media all and (max-width:720px) {
    .nav{
        height:max-content;
         
     }
 
    .navLinks {
   
        display:block;
        justify-content: flex-end;
        align-items: flex-start;
      
        width: 100%;
        height:max-content;
    }

    
    .dropDownBar{
   
        display:flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: nowrap;
     
    }


    .navLogo{
        display: flex;

       
            margin: 5px;
            padding: 5px;
        
      
    }


    .Menu {
        display:flex;
    }  


    .menuLinks{
        display: flex;
        justify-content: center;
        align-items: center;
        position:sticky;
       
        font-size: 20px;
        background-color:  rgba(0, 0, 0, 0.4);
        color: white;
    
    }


    .navList{
        display: none;
    }

    .menuLinks ul{
        flex-direction: column;
        flex-wrap: nowrap;
        list-style-type: none;
        
        
        margin: 0;
        padding: 0;
    }


    .menuLinks li{
        margin: 5px;
        padding: 5px;
    }

   
 
}




@media (prefers-reduced-motion: no-preference) {
    .navLogo:hover{
      animation: logo-spin infinite 3s linear;
    
    }
  }

  @keyframes logo-spin {
    from {
      transform: rotateY(0deg);
      
    }
    to {
      transform: rotateY(360deg);
     
    }
    
  }
